@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
:root {
  --bg-color: #0a0a0a;
  --terminal-bg: #121212;
  --text-color: #00ff00;
  --accent-color: #00cc00;
  --secondary-color: #0088ff;
  --warning-color: #ffcc00;
  --error-color: #ff3333;
  --board-color: #1a1a1a;
  --chip-color: #2c2c2c;
  --pin-color: #666;
  --led-off: #333;
  --transition-speed: 0.3s;
  --robot-primary: #0088ff;
  --robot-secondary: #00aaff;
  --robot-accent: #ff3800;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Share Tech Mono', 'Courier New', monospace;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  line-height: 1.6;
  overflow-x: hidden;
  position: relative;
}

/* Scene and content containers */
.scene {
  min-height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 1;
  padding: 40px 0;
}

.content-container {
  max-width: 1200px;
  width: 100%;
  padding: 0 2rem;
  position: relative;
  z-index: 2;
}

.title-effect {
  color: var(--secondary-color);
  font-weight: bold;
}

.cta-button {
  display: inline-block;
  padding: 0.8rem 1.5rem;
  background-color: var(--terminal-bg);
  color: var(--text-color);
  text-decoration: none;
  border: 1px solid var(--accent-color);
  font-size: 1rem;
  font-weight: bold;
  margin-right: 1rem;
  margin-top: 1rem;
  transition: all var(--transition-speed) ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  border-radius: 4px;
}

.cta-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 255, 0, 0.2), transparent);
  transition: left 0.5s ease;
}

.cta-button:hover::after {
  left: 100%;
}

.cta-button:hover {
  background-color: rgba(0, 255, 0, 0.1);
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
  transform: translateY(-3px);
}

.section-title {
  font-size: 2.5rem;
  margin-bottom: 3rem;
  position: relative;
  display: inline-block;
  text-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
}

.section-title::before {
  content: '[';
  color: var(--secondary-color);
  margin-right: 10px;
}

.section-title::after {
  content: ']';
  color: var(--secondary-color);
  margin-left: 10px;
}

/* Terminal Component */
.terminal {
  background-color: var(--terminal-bg);
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0 4px 20px rgba(0, 255, 0, 0.1);
  position: relative;
  overflow: hidden;
  transition: transform 0.3s, box-shadow 0.3s;
}

.terminal:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 255, 0, 0.2);
}

.terminal::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 24px;
  background: rgba(255, 255, 255, 0.05);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.terminal-header {
  position: absolute;
  top: 5px;
  left: 10px;
  display: flex;
  grid-gap: 6px;
  gap: 6px;
}

.terminal-button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.terminal-button.close {
  background-color: var(--error-color);
}

.terminal-button.minimize {
  background-color: var(--warning-color);
}

.terminal-button.maximize {
  background-color: var(--accent-color);
}

.terminal-content {
  margin-top: 15px;
  padding-top: 10px;
}

.terminal-text {
  overflow: hidden;
  white-space: nowrap;
  margin: 0;
  padding: 0;
  animation: typing 3s steps(60, end);
}

.blinking-cursor {
  display: inline-block;
  width: 10px;
  height: 18px;
  background-color: var(--text-color);
  animation: blink 1s infinite;
  vertical-align: middle;
  margin-left: 4px;
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

@keyframes blink {
  0%, 49% { opacity: 1; }
  50%, 100% { opacity: 0; }
}

/* Animations */
@keyframes pulse {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 1; }
}

@keyframes speak {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(20%); }
  50% { transform: translateX(-20%); }
  75% { transform: translateX(5%); }
}

@keyframes speakWide {
  0%, 100% { width: 50px; left: 0; }
  50% { width: 30px; left: 10px; }
}

@keyframes robotFloat {
  0%, 100% { transform: translateY(0) rotateY(0); }
  25% { transform: translateY(-10px) rotateY(15deg); }
  50% { transform: translateY(5px) rotateY(0); }
  75% { transform: translateY(-5px) rotateY(-15deg); }
}

@keyframes eyeMove {
  0%, 100% { transform: translate(0, 0); }
  25% { transform: translate(3px, 3px); }
  50% { transform: translate(-3px, 0); }
  75% { transform: translate(0, -3px); }
}

@keyframes navGlow {
  0%, 100% { transform: translateX(-100%); }
  50% { transform: translateX(100%); }
}

@keyframes scrollText {
  0% { transform: translateX(100%); }
  100% { transform: translateX(-100%); }
}

@keyframes leftArmMove {
  0%, 100% { transform: rotate(5deg); }
  20%, 80% { transform: rotate(15deg); }
  40%, 60% { transform: rotate(0deg); }
}

@keyframes rightArmMove {
  0%, 100% { transform: rotate(-5deg); }
  20%, 80% { transform: rotate(-15deg); }
  40%, 60% { transform: rotate(0deg); }
}

@keyframes randomBlink {
  0%, 100% { background: var(--led-off); }
  15% { background: var(--robot-primary); box-shadow: 0 0 5px var(--robot-primary); }
  30% { background: var(--led-off); }
  45% { background: var(--led-off); }
  60% { background: var(--robot-primary); box-shadow: 0 0 5px var(--robot-primary); }
  75% { background: var(--led-off); }
}

@keyframes trackMove {
  0% { left: -20px; opacity: 0.7; }
  100% { left: 100%; opacity: 0; }
}

/* Visibility for animation elements */
.timeline-item, .robot-project, .language-item, .interest-item {
  transform: translateY(50px);
  opacity: 0;
  transition: all 0.6s ease;
}

.timeline-item.visible, 
.robot-project.visible, 
.language-item.visible, 
.interest-item.visible {
  transform: translateY(0);
  opacity: 1;
}

/* Responsive styles */
@media (max-width: 768px) {
  .timeline::after {
    left: 31px;
  }
  
  .timeline-item {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
  
  .timeline-item.right {
    left: 0;
  }
  
  .timeline-item.left::after,
  .timeline-item.right::after {
    left: 21px;
  }
  
  .hero-content h1 {
    font-size: 2.5rem;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .robot-diagram-container {
    height: 400px;
  }
  
  .robot-model-container {
    min-height: 300px;
  }
  
  .robot-model {
    width: 220px;
    height: 280px;
  }
}

@media (max-width: 480px) {
  .hero-content h1 {
    font-size: 2rem;
  }

  .section-title {
    font-size: 1.5rem;
  }

  .cta-button {
    width: 100%;
    margin-right: 0;
    margin-bottom: 1rem;
    text-align: center;
  }

  .robot-diagram-container {
    height: 350px;
  }
  
  .robot-part {
    font-size: 0.7rem;
  }
  
  .robot-part.cpu {
    width: 90px;
    height: 90px;
  }
  
  .robot-part.sensors,
  .robot-part.communication,
  .robot-part.motor-controller,
  .robot-part.power {
    width: 80px;
    height: 50px;
  }
}

