.profile-picture-container {
  width: 300px;
  height: 300px;
  position: relative;
  transition: transform 0.3s ease-out;
  transform-style: preserve-3d;
}

.profile-frame {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 20px;
  background: var(--terminal-bg);
  border-radius: 10px;
  overflow: hidden;
}

.profile-image {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 5px;
}

.profile-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: contrast(110%) brightness(110%);
}

.frame-corner {
  position: absolute;
  width: 20px;
  height: 20px;
  border: 2px solid var(--robot-primary);
  z-index: 2;
}

.top-left {
  top: 0;
  left: 0;
  border-right: none;
  border-bottom: none;
}

.top-right {
  top: 0;
  right: 0;
  border-left: none;
  border-bottom: none;
}

.bottom-left {
  bottom: 0;
  left: 0;
  border-right: none;
  border-top: none;
}

.bottom-right {
  bottom: 0;
  right: 0;
  border-left: none;
  border-top: none;
}

.frame-edge {
  position: absolute;
  background: var(--robot-primary);
  opacity: 0.5;
  z-index: 2;
}

.top, .bottom {
  height: 2px;
  width: calc(100% - 40px);
  left: 20px;
}

.left, .right {
  width: 2px;
  height: calc(100% - 40px);
  top: 20px;
}

.top { top: 0; }
.right { right: 0; }
.bottom { bottom: 0; }
.left { left: 0; }

.scanning-line {
  position: absolute;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    var(--robot-primary) 50%, 
    transparent 100%
  );
  top: 0;
  left: 0;
  z-index: 3;
  animation: scan 3s linear infinite;
  opacity: 0.7;
  box-shadow: 0 0 10px var(--robot-primary);
}

.frame-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    45deg,
    transparent 0%,
    rgba(0, 136, 255, 0.1) 50%,
    transparent 100%
  );
  pointer-events: none;
  z-index: 2;
}

@keyframes scan {
  0% { transform: translateY(0); }
  100% { transform: translateY(300px); }
}

/* Glitch effect on hover */
.profile-image:hover img {
  animation: glitch 0.5s infinite;
}

@keyframes glitch {
  0% { transform: translate(0); }
  20% { transform: translate(-2px, 2px); }
  40% { transform: translate(-2px, -2px); }
  60% { transform: translate(2px, 2px); }
  80% { transform: translate(2px, -2px); }
  100% { transform: translate(0); }
}
#hero {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
}

.hero-content {
  text-align: center;
  transition: transform 0.5s;
  z-index: 2;
  width: 100%;
}

.hero-flex {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  max-width: 1200px;
  flex-wrap: wrap;
  grid-gap: 2rem;
  gap: 2rem;
  align-items: center;
}

.hero-text {
  flex: 1 1;
  min-width: 300px;
  text-align: left;
  padding: 2rem;
}

.hero-profile {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 300px;
  perspective: 1000px;
}

.cta-buttons {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  margin-top: 2rem;
}

.hero-content h1 {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem;
  text-shadow: 0 0 15px rgba(0, 255, 0, 0.7);
  transition: transform 0.5s;
  cursor: pointer;
}

.hero-content h1:hover {
  transform: scale(1.05);
  text-shadow: 0 0 25px rgba(0, 255, 0, 0.9);
}

.hero-subtitle {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  opacity: 0.8;
}

/* 3D Robot Model */
.robot-model-container {
  flex: 1 1;
  min-width: 300px;
  min-height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  perspective: 1000px;
}

.robot-model {
  width: 280px;
  height: 350px;
  position: relative;
  transform-style: preserve-3d;
  animation: robotFloat 5s ease-in-out infinite;
}

.robot-model-part {
  position: absolute;
  transform-style: preserve-3d;
  transition: transform 0.5s;
}

.robot-model-head {
  width: 120px;
  height: 100px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 20px;
  top: 30px;
  left: 80px;
  z-index: 3;
  transform-style: preserve-3d;
  box-shadow: 0 0 20px rgba(0, 136, 255, 0.5);
}

.robot-model-eye {
  position: absolute;
  width: 25px;
  height: 25px;
  background: #000;
  border-radius: 50%;
  top: 30px;
  border: 2px solid var(--robot-primary);
  overflow: hidden;
  z-index: 4;
}

.robot-model-eye.left {
  left: 25px;
}

.robot-model-eye.right {
  right: 25px;
}

.robot-model-eye-light {
  position: absolute;
  width: 10px;
  height: 10px;
  background: var(--robot-primary);
  border-radius: 50%;
  top: 7px;
  left: 7px;
  box-shadow: 0 0 10px var(--robot-primary);
  animation: eyeMove 3s infinite alternate;
}

.robot-model-mouth {
  position: absolute;
  width: 50px;
  height: 10px;
  background: #000;
  bottom: 20px;
  left: 35px;
  border-radius: 5px;
  overflow: hidden;
}

.robot-model-mouth-line {
  position: absolute;
  height: 2px;
  width: 50px;
  background: var(--robot-primary);
  top: 4px;
  animation: speakWide 2s infinite;
}

.robot-model-body {
  width: 180px;
  height: 150px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 20px;
  top: 120px;
  left: 50px;
  z-index: 2;
  box-shadow: 0 0 20px rgba(0, 136, 255, 0.3);
}

.robot-model-chest-light {
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #000;
  top: 30px;
  left: 65px;
  overflow: hidden;
  border: 2px solid var(--robot-primary);
}

.robot-model-light-effect {
  position: absolute;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, var(--robot-primary) 0%, transparent 70%);
  opacity: 0.7;
  animation: pulse 2s infinite;
}

.robot-model-panel {
  position: absolute;
  bottom: 20px;
  width: 120px;
  height: 30px;
  background: #000;
  left: 30px;
  border-radius: 5px;
  border: 1px solid var(--robot-primary);
}

.robot-model-button-row {
  display: flex;
  justify-content: space-around;
  margin-top: 5px;
}

.robot-model-button {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: var(--led-off);
  animation: randomBlink 5s infinite;
}

.robot-model-button:nth-child(2) {
  animation-delay: 0.7s;
}

.robot-model-button:nth-child(3) {
  animation-delay: 1.3s;
}

.robot-model-button:nth-child(4) {
  animation-delay: 1.9s;
}

.robot-model-button:nth-child(5) {
  animation-delay: 2.5s;
}

.robot-model-arm {
  width: 30px;
  height: 120px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 15px;
  position: absolute;
  top: 130px;
  z-index: 1;
}

.robot-model-arm.left {
  left: 10px;
  transform-origin: top center;
  animation: leftArmMove 10s infinite;
}

.robot-model-arm.right {
  right: 10px;
  transform-origin: top center;
  animation: rightArmMove 10s infinite;
}

.robot-model-hand {
  position: absolute;
  width: 40px;
  height: 40px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 10px;
  bottom: -20px;
  left: -5px;
}

.robot-model-finger {
  position: absolute;
  width: 8px;
  height: 15px;
  background: var(--robot-primary);
  border-radius: 4px;
  bottom: -5px;
}

.finger-1 { left: 5px; }
.finger-2 { left: 16px; }
.finger-3 { left: 27px; }

.robot-model-leg {
  width: 40px;
  height: 100px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 15px;
  position: absolute;
  top: 260px;
  z-index: 1;
}

.robot-model-leg.left {
  left: 60px;
}

.robot-model-leg.right {
  right: 60px;
}

.robot-model-foot {
  position: absolute;
  width: 50px;
  height: 20px;
  background: var(--robot-primary);
  bottom: -10px;
  left: -5px;
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(0, 136, 255, 0.5);
}

@media (max-width: 768px) {
  .hero-content h1 {
    font-size: 2.5rem;
  }
  
  .robot-model-container {
    min-height: 300px;
  }
  
  .robot-model {
    width: 220px;
    height: 280px;
  }

  .hero-flex {
    flex-direction: column-reverse;
    text-align: center;
  }
  
  .hero-text {
    margin-top: 2rem;
  }
  
  .cta-buttons {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .hero-content h1 {
    font-size: 2rem;
  }
}
/* About component specific styles */
#about {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.circuit-board {
  background-color: var(--board-color);
  border-radius: 10px;
  padding: 30px;
  margin-bottom: 40px;
  position: relative;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  border: 1px solid var(--accent-color);
  overflow: hidden;
  transition: transform 0.5s;
  transform-style: preserve-3d;
}

.circuit-board:hover {
  transform: translateY(-10px) rotateX(5deg);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.7);
}

.circuit-board::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
      linear-gradient(90deg, transparent 99%, var(--accent-color) 99.5%, transparent 100%),
      linear-gradient(0deg, transparent 99%, var(--accent-color) 99.5%, transparent 100%);
  background-size: 20px 20px;
  opacity: 0.1;
  pointer-events: none;
}

.chip {
  background-color: var(--chip-color);
  border-radius: 4px;
  padding: 15px;
  margin-bottom: 20px;
  position: relative;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.chip:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.5);
}

.chip::before {
  content: '';
  position: absolute;
  top: -5px;
  left: 10px;
  width: 30px;
  height: 5px;
  background-color: var(--pin-color);
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}

.chip::after {
  content: '';
  position: absolute;
  bottom: -5px;
  right: 10px;
  width: 30px;
  height: 5px;
  background-color: var(--pin-color);
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.chip-title {
  font-size: 1.2rem;
  margin-bottom: 10px;
  color: var(--secondary-color);
}

.pin-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.pin {
  width: 10px;
  height: 10px;
  background-color: var(--pin-color);
  border-radius: 50%;
}

/* Skill meters */
.skill-meter {
  width: 100%;
  height: 20px;
  background-color: var(--terminal-bg);
  border-radius: 10px;
  overflow: hidden;
  margin-top: 10px;
  position: relative;
  border: 1px solid var(--accent-color);
  margin-bottom: 15px;
}

.skill-progress {
  height: 100%;
  background: linear-gradient(90deg, var(--accent-color), var(--secondary-color));
  border-radius: 10px;
  width: 0; /* Will be set dynamically with JS */
  transition: width 1.5s ease;
}

.skill-dots {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  align-items: center;
}

.skill-dot {
  width: 3px;
  height: 10px;
  background-color: rgba(0, 0, 0, 0.3);
}
.timeline {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
}

.timeline::after {
  content: '';
  position: absolute;
  width: 2px;
  background-color: var(--robot-primary);
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -1px;
  box-shadow: 0 0 10px var(--robot-primary);
}

.timeline-item {
  position: relative;
  width: 50%;
  padding: 1rem 2rem;
  box-sizing: border-box;
  margin-bottom: 2rem;
  transform: translateY(50px);
  opacity: 0;
  transition: all 0.8s ease;
  cursor: pointer;
}

.timeline-item.visible {
  transform: translateY(0);
  opacity: 1;
}

.timeline-item:hover {
  transform: scale(1.05);
  z-index: 10;
}

.timeline-item.left {
  left: 0;
}

.timeline-item.right {
  left: 50%;
}

.timeline-item::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: var(--terminal-bg);
  border: 3px solid var(--robot-primary);
  top: 1rem;
  z-index: 1;
  box-shadow: 0 0 10px var(--robot-primary);
}

.timeline-item.left::after {
  right: -13px;
}

.timeline-item.right::after {
  left: -13px;
}

.timeline-content {
  padding: 1.5rem;
  background-color: var(--terminal-bg);
  border-radius: 6px;
  box-shadow: 0 0 15px rgba(0, 255, 0, 0.1);
  position: relative;
  border: 1px solid var(--robot-primary);
  transition: transform 0.3s, box-shadow 0.3s;
}

.timeline-content:hover {
  box-shadow: 0 0 25px rgba(0, 136, 255, 0.3);
}

.timeline-date {
  display: inline-block;
  padding: 0.3rem 0.8rem;
  background-color: var(--chip-color);
  color: var(--robot-primary);
  font-size: 0.9rem;
  border-radius: 4px;
  font-weight: bold;
  margin-bottom: 0.5rem;
  border: 1px solid var(--robot-primary);
}

.timeline-title {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  color: var(--robot-primary);
}

@media (max-width: 768px) {
  .timeline::after {
    left: 31px;
  }
  
  .timeline-item {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
  
  .timeline-item.right {
    left: 0;
  }
  
  .timeline-item.left::after,
  .timeline-item.right::after {
    left: 21px;
  }
}
.robot-project {
  background-color: var(--terminal-bg);
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid var(--robot-primary);
  transition: all 0.3s;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  position: relative;
  transform: translateY(50px);
  opacity: 0;
  height: 400px;
  cursor: pointer;
}

.robot-project.visible {
  transform: translateY(0);
  opacity: 1;
}

.robot-project:hover {
  transform: scale(1.03);
  box-shadow: 0 15px 30px rgba(0, 136, 255, 0.3);
}

.project-image {
  height: 200px;
  overflow: hidden;
  position: relative;
  background: var(--board-color);
}

.project-robot {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.project-details {
  padding: 20px;
}

.project-title {
  font-size: 1.5rem;
  color: var(--robot-primary);
  margin-bottom: 10px;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 10px;
  gap: 10px;
  margin: 15px 0;
}

.tech-tag {
  background: var(--chip-color);
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 0.8rem;
  border: 1px solid var(--robot-primary);
}

.project-links {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
}

.project-link {
  padding: 8px 15px;
  background: var(--chip-color);
  color: var(--robot-primary);
  text-decoration: none;
  border-radius: 5px;
  border: 1px solid var(--robot-primary);
  transition: all 0.3s;
}

.project-link:hover {
  background: var(--robot-primary);
  color: #000;
}
.robotics-projects {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
  margin-top: 2rem;
}

@media (max-width: 480px) {
  .robotics-projects {
    grid-template-columns: 1fr;
  }
}
/* Interactive Robot Diagram */
.robot-diagram-container {
  width: 100%;
  height: 500px;
  position: relative;
  margin: 40px 0;
  background: var(--board-color);
  border-radius: 10px;
  border: 1px solid var(--robot-primary);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.7);
  overflow: hidden;
}

.robot-diagram {
  width: 100%;
  height: 100%;
  position: relative;
}

.robot-part {
  position: absolute;
  background: var(--chip-color);
  border: 1px solid var(--robot-primary);
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
  font-size: 0.9rem;
  color: var(--robot-primary);
  text-align: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.robot-part:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 136, 255, 0.4);
  z-index: 10;
}

.robot-part.cpu {
  width: 120px;
  height: 120px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
}

.robot-part.motor-controller {
  width: 100px;
  height: 70px;
  bottom: 100px;
  left: 20%;
}

.robot-part.power {
  width: 100px;
  height: 70px;
  bottom: 100px;
  right: 20%;
}

.robot-part.sensors {
  width: 100px;
  height: 70px;
  top: 100px;
  left: 20%;
}

.robot-part.communication {
  width: 100px;
  height: 70px;
  top: 100px;
  right: 20%;
}

.robot-connection {
  position: absolute;
  background: var(--robot-primary);
  opacity: 0.5;
  transition: all 0.3s;
  z-index: 1;
}

.robot-connection.active {
  opacity: 1;
  box-shadow: 0 0 10px var(--robot-primary);
}

/* Create vertical and horizontal connections */
.vert-connection {
  width: 2px;
  height: 80px;
}

.horiz-connection {
  height: 2px;
  width: 80px;
}

.connection-cpu-sensors {
  top: 170px;
  left: calc(20% + 50px);
}

.connection-cpu-communication {
  top: 170px;
  right: calc(20% + 50px);
}

.connection-cpu-motors {
  bottom: 170px;
  left: calc(20% + 50px);
}

.connection-cpu-power {
  bottom: 170px;
  right: calc(20% + 50px);
}

.robot-part-tooltip {
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  color: var(--robot-primary);
  padding: 10px;
  border-radius: 5px;
  width: 200px;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s;
  z-index: 20;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  border: 1px solid var(--robot-primary);
}

.robot-part:hover + .robot-part-tooltip {
  opacity: 1;
}

/* Interactive Robot Simulation */
.robot-simulation {
  width: 100%;
  height: 300px;
  background: var(--board-color);
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  margin: 30px 0;
  border: 1px solid var(--robot-primary);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.7);
}

.simulation-robot {
  position: absolute;
  width: 50px;
  height: 50px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 10px;
  bottom: 20px;
  left: 20px;
  transition: all 0.5s;
  cursor: pointer;
  z-index: 2;
}

.simulation-robot:hover {
  box-shadow: 0 0 15px var(--robot-primary);
}

.simulation-control {
  position: absolute;
  bottom: 20px;
  right: 20px;
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  z-index: 10;
}

.control-btn {
  width: 40px;
  height: 40px;
  background: var(--chip-color);
  border: 1px solid var(--robot-primary);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--robot-primary);
  font-size: 1.5rem;
  cursor: pointer;
  transition: all 0.3s;
}

.control-btn:hover {
  background: var(--robot-primary);
  color: #000;
}

.simulation-obstacle {
  position: absolute;
  width: 40px;
  height: 40px;
  background: var(--chip-color);
  border: 1px solid var(--error-color);
  border-radius: 5px;
}

.obstacle-1 {
  bottom: 20px;
  left: 200px;
}

.obstacle-2 {
  bottom: 80px;
  left: 400px;
}

.obstacle-3 {
  bottom: 150px;
  left: 300px;
}

.simulation-sensor {
  position: absolute;
  width: 70px;
  height: 40px;
  background: transparent;
  border-left: 2px dashed var(--robot-primary);
  border-top: 2px dashed var(--robot-primary);
  border-bottom: 2px dashed var(--robot-primary);
  border-radius: 20px 0 0 20px;
  top: 5px;
  right: -10px;
  opacity: 0;
  transition: opacity 0.3s;
}

.simulation-robot:hover .simulation-sensor {
  opacity: 0.7;
}

.simulation-path {
  position: absolute;
  height: 2px;
  bottom: 45px;
  left: 70px;
  background: var(--robot-primary);
  opacity: 0.5;
  transform-origin: left;
  transform: scaleX(0);
  transition: transform 0.5s;
}

@media (max-width: 768px) {
  .robot-diagram-container {
    height: 400px;
  }
}

@media (max-width: 480px) {
  .robot-diagram-container {
    height: 350px;
  }
  
  .robot-part {
    font-size: 0.7rem;
  }
  
  .robot-part.cpu {
    width: 90px;
    height: 90px;
  }
  
  .robot-part.sensors,
  .robot-part.communication,
  .robot-part.motor-controller,
  .robot-part.power {
    width: 80px;
    height: 50px;
  }
}
.languages-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
  margin-top: 2rem;
}

.language-item {
  background-color: var(--terminal-bg);
  padding: 1.5rem;
  border-radius: 8px;
  transform: translateY(50px);
  opacity: 0;
  transition: all 0.6s ease;
  border: 1px solid var(--robot-primary);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.language-item.visible {
  transform: translateY(0);
