  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
  position: relative;
}

.contact-robot {
  position: absolute;
  top: -100px;
  right: 50px;
  width: 150px;
  height: 150px;
  z-index: 1;
}

.contact-text {
  text-align: center;
  max-width: 600px;
  margin-bottom: 2rem;
