.nav-container {
  position: fixed;
  top: 2rem;
  right: 2rem;
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav-toggle {
  width: 40px;
  height: 40px;
  background-color: var(--terminal-bg);
  border: 1px solid var(--accent-color);
  color: var(--text-color);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 1.5rem;
  border-radius: 4px;
  box-shadow: 0 2px 10px rgba(0, 255, 0, 0.2);
  position: relative;
  overflow: hidden;
}

.nav-toggle:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, transparent 45%, var(--accent-color) 50%, transparent 55%);
  animation: navGlow 3s infinite;
  opacity: 0.3;
}

.nav-menu {
  position: fixed;
  top: 0;
  right: -300px;
  width: 300px;
  height: 100vh;
  background-color: var(--terminal-bg);
  padding: 2rem;
  transition: right var(--transition-speed) ease;
  z-index: 101;
  border-left: 1px solid var(--accent-color);
  box-shadow: -5px 0 20px rgba(0, 255, 0, 0.1);
}

.nav-menu.active {
  right: 0;
}

.nav-close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  background: none;
  border: 1px solid var(--accent-color);
  color: var(--text-color);
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 4px;
}

.nav-links {
  margin-top: 3rem;
  list-style: none;
}

.nav-links li {
  margin-bottom: 1.5rem;
}

.nav-links a {
  color: var(--text-color);
  text-decoration: none;
  font-size: 1.2rem;
  position: relative;
  display: inline-block;
  padding: 0.5rem 0;
  transition: all 0.3s;
}

.nav-links a::before {
  content: '> ';
  opacity: 0;
  transition: opacity var(--transition-speed) ease;
}

.nav-links a:hover {
  transform: translateX(10px);
  color: var(--accent-color);
}

.nav-links a:hover::before {
  opacity: 1;
}

#robot-assistant {
  position: fixed;
  width: 180px;
  height: 250px;
  z-index: 1000;
  cursor: pointer;
  transform-style: preserve-3d;
  perspective: 1000px;
  will-change: transform;
  pointer-events: auto;
  margin-top: 70px; /* Add margin from the top */
}

#robot-assistant.moving {
  animation: moveShake 0.5s infinite;
}

.robot-container {
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.5s ease;
}

.robot-head {
  position: absolute;
  width: 80px;
  height: 70px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 15px 15px 10px 10px;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  overflow: hidden;
  transition: all 0.3s;
}

.robot-eye-container {
  position: relative;
  display: flex;
  justify-content: space-around;
  padding: 15px 0 0 0;
}

.robot-eye {
  position: relative;
  width: 20px;
  height: 20px;
  background: #000;
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid var(--robot-primary);
  transition: all 0.3s;
}

.robot-pupil {
  position: absolute;
  width: 10px;
  height: 10px;
  background: var(--robot-primary);
  border-radius: 50%;
  top: 5px;
  left: 5px;
  transition: all 0.2s;
}

.robot-mouth {
  position: absolute;
  width: 40px;
  height: 8px;
  background: #000;
  bottom: 15px;
  left: 20px;
  border-radius: 4px;
  overflow: hidden;
}

.robot-mouth-line {
  position: absolute;
  width: 100%;
  height: 2px;
  background: var(--robot-primary);
  top: 3px;
  animation: speak 2s infinite;
}

.robot-antenna {
  position: absolute;
  width: 6px;
  height: 25px;
  background: var(--pin-color);
  top: 5px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.robot-antenna:after {
  content: '';
  position: absolute;
  width: 12px;
  height: 12px;
  background: var(--robot-primary);
  border-radius: 50%;
  top: -10px;
  left: -3px;
  animation: pulse 2s infinite;
  box-shadow: 0 0 10px var(--robot-primary);
}

.robot-body {
  position: absolute;
  width: 120px;
  height: 100px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 15px;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 10px;
  z-index: 1;
}

.robot-body-display {
  width: 90%;
  height: auto;
  min-height: 40px;
  max-height: 80px;
  background: var(--terminal-bg);
  border: 2px solid var(--robot-primary);
  border-radius: 8px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 0 10px var(--robot-primary) inset;
  padding: 5px;
}

.robot-message-container {
  position: fixed;
  min-width: 250px;
  max-width: 350px;
  width: auto;
  background: var(--terminal-bg);
  border: 2px solid var(--robot-primary);
  border-radius: 12px;
  padding: 20px;
  z-index: 1001;
  box-shadow: 0 0 25px rgba(0, 136, 255, 0.3);
  opacity: 0;
  transition: all 0.3s ease;
  pointer-events: none;
  margin: 0 20px;
  transform: translate(-50%, -120%);
  word-wrap: break-word;
  overflow-wrap: break-word;
}

.robot-message-container::before {
  content: '';
  position: absolute;
  bottom: -12px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
  border-top: 12px solid var(--robot-primary);
}

.robot-message-container::after {
  content: '';
  position: absolute;
  bottom: -9px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid var(--terminal-bg);
}

.robot-message-container.visible {
  opacity: 1;
  transform: translate(-50%, -50%);
}

.robot-display-text {
  font-size: 12px;
  line-height: 1.4;
  color: var(--robot-primary);
  text-align: left;
