}

.dialog-options {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}

.dialog-options button {
  background: var(--chip-color);
  border: 1px solid var(--robot-primary);
  color: var(--robot-primary);
  padding: 8px 12px;
  border-radius: 5px;
